
import { getListTask,getDetailInfo} from '../../service/service.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
data(){
return{
infoList:[],
detailInfo:{},
showDialog:false,
show:false
}
},
directives: {
TransferDom
},
components: {
Group,
Cell,
Datetime,
XInput,
XTextarea,
Confirm
},
methods:{
confirm(){
this.showDialog = true;

},
reject(){

},
onCancel () {

},
onConfirm (msg) {

},
change(value){
console.log(value);
}
},
created(){
this.detailInfo = this.$route.query.info;
let token  = localStorage.getItem("token");
let code = this.$route.query.info.businessKey;
console.log(token);
getListTask(this.detailInfo.businessKey,token).then((result)=>{
this.infoList = result;

})
let time = new Date().getTime();
getDetailInfo(time,token,code).then((result)=>{
if(result.length>=1){
this.detailInfo = JSON.parse(result[0].json_data);
}
var t = JSON.parse(result[0].json_data);
console.log(t);
})
},
mounted(){
var self = this;
self.mescroll = new MeScroll("mescroll",{
up:{
isBounce:false,
use:false
},
down:{
use:false
}
})



}

}




